
#volverABuscar{
  background-color: #0A0908;
    color:#FFEE32;
    padding: 1em;
    margin-left: 1em;
    border: 1px solid #0A0908;
    font-weight: bold;
    width: 40%;
    margin: 0 auto;
    margin-top: 1em;
}

@media only screen and (min-width: 1200px) {
  .App {
    text-align: center;
  }
  h4{
    background-color: #0A0908;
    padding: 0.2em;
  }
  h5{
    background-color: #0A0908;
  }
  #links{
    background-color: #0A0908;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  *{
    background-color: #FFEE32;
    text-align: center;
  }
  
  button:hover{
    cursor: pointer;
  }
  /*BARRA DE BUSQUEDA*/
  #botonBusqueda{
    background-color: #0A0908;
    color:#FFEE32;
    padding: 0.4em;
    margin-left: 1em;
    border: 1px solid #0A0908;
    font-weight: bold;
  }
  #barraDeBusqueda{
    padding: 0.3em;
    width: 30%;
    border: 2px solid #0A0908;
    border-radius: none;
  }
  
  /*FIN BARRA DE BUSQUEDA*/
  h1{
    color: #0A0908;
  }
  
  h3{
    color: #FFEE32;
    background-color: #0A0908;
  }
  
  .ContenedorDeResultados {
    display: grid;
    grid-template-columns: 33% 33% 33%;
    width: 100%;
    height: 100%;
    background-color: #FFEE32;
    margin-bottom: 2em;
  }
  
  .resultado{
    text-align: center;
    display: block;
    background-color: #0A0908;
    margin: 1em;
    width: auto;
    height: auto;
    padding: 0.1em;
    box-sizing: content-box;
    padding-bottom: 1em;
  }
  .botonVerMas{
    display: block;
    margin: auto;
    border: none;
    padding: 0.6em;
    background-color: #FFD100;
    font-weight: bold;
  }
  
  
  .imagenPortada{
    margin-bottom: 0.5em;
  }
  
  /* COMPONENTE PELICULA*/
  
  .contenedorIndividual{
    display: grid;
    margin: 0 auto;
    width: 90%;
    margin-top: 2em;
    grid-template-columns: 100%;
    margin-top: -1;
  }
  
  #contenidoPelicula{
    background-color: #0A0908;
    color: #FFD100;
    margin: auto;
    width: 60%;
    text-align: center;
    margin-bottom: 1em;
  }
  
  #volverABuscar{
    background-color: #0A0908;
      color:#FFEE32;
      padding: 1em;
      margin-left: 1em;
      border: 1px solid #0A0908;
      font-weight: bold;
      width: 40%;
      margin: 0 auto;
      margin-top: 1em;
  }
}
